import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import CourseIcon from "./CourseIcon";

function CourseMenu() {
  const [coursesDisplayed, setCoursesDisplayed] = useState([]);
  useEffect(() => {
    api.get(`/coursesData`).then((response) => {
      setCoursesDisplayed(response.data);
      console.log(response.data);
    });
  }, []);
  console.log(coursesDisplayed);

  return (
    <div>
      <div className="title-container">
        <h1 className="fancy-title">Welcome to Our Courses</h1>
      </div>
      <div>
        {coursesDisplayed.map((course, index) => {
          return (
            <>
              {course.courseIsActive && (
                <CourseIcon
                  img={
                    "https://qaleelo-assets.s3.us-east-2.amazonaws.com/" +
                    course.courseTitle.replace(/\s/g, "") +
                    "/courseImage/" +
                    course.courseTitle.replace(/\s/g, "") +
                    ".png"
                  }
                  title={course.courseTitle}
                  description={course.courseDescription}
                ></CourseIcon>
              )}
            </>
          );
        })}
      </div>
    </div>
  );
}

export default CourseMenu;
